import React, { FC, useContext } from 'react';
import { Actions } from '../../reducer';
import '../../scss/fullScreenButton.scss';
import { AppContext } from '../app';


const FullScreenButton: FC = () => {
	const context = useContext(AppContext);

	function onClick(): void {
		context.dispatch({
			type: Actions.SetFullScreen,
			fullScreen: !context.state.fullScreen
		});
	}

	return <button onClick={onClick} className="full-screen-button">
		<i className="material-icons">{context.state.fullScreen ? 'fullscreen_exit' : 'fullscreen'}</i>
	</button>
}

export default FullScreenButton;
